<template>
	<view>
		<uni-ec-canvas class="uni-ec-canvas" id="line-chart" ref="canvas" canvas-id="lazy-load-chart"
			:ec="ec"></uni-ec-canvas>
	</view>
</template>

<script>
	import uniEcCanvas from '../components/uni-ec-canvas/uni-ec-canvas'
	import * as echarts from '../components/uni-ec-canvas/echarts'
	let chart = null
	export default {
		components: {
			uniEcCanvas
		},
		data() {
			return {
				ec: {
					lazyLoad: true,
				},
			};
		},
		mounted() {
			this.$refs.canvas.init(this.initChart);
		},
		methods: {
			initChart(canvas, width, height, canvasDpr) {
				console.log(canvas, width, height, canvasDpr)
				chart = echarts.init(canvas, null, {
					width: width,
					height: height,
					devicePixelRatio: canvasDpr
				})
				canvas.setChart(chart);
				let option = {
					title: {
						text: '',

					},
					tooltip: {
						trigger: 'axis',
						formatter: '{b}\r\n{c0}人',
						axisPointer: {
							type: 'line',
							axis: 'x',
							label: {
								backgroundColor: '#000000'
							}
						}
					},
					grid: {
						left: '6%',
						right: '6%',
						top: '6%',
						bottom: '6%',
						containLabel: true
					},
					xAxis: {
						name: "日期",
						type: 'category',
						boundaryGap: false,
						data: ['2-12', '2-14', '2-16', '2-18', '2-20', '2-22', '2-24'],
						axisLine: {
							// x轴
							show: true,
						},
						axisTick: {
							// x轴刻度线
							show: true,
						},
						splitLine: {
							// 网格线
							show: false
						}
					},
					yAxis: {
						type: 'value',
						axisLine: {
							// y轴
							show: true,
						},
						axisTick: {
							// y轴刻度线
							show: false
						},
						splitLine: {
							// 网格线
							show: false
						}
					},
					series: [{
						name: '浏览量',
						type: 'line',
						smooth: true,
						lineStyle: {
							color: '#EF5959'
						},
						data: [120, 132, 101, 134, 90, 230, 210]
					}]
				};
				chart.setOption(option);
				return chart
			},
		},
		onUnload() {
			chart = null
		},
		onLoad() {
			this.$refs.canvas.init(this.initChart)
		}
	}
</script>

<style scoped>
	.uni-ec-canvas {
		width: 100%;
		height: 100vh;
		display: block;
	}
</style>